


































import {Component, Prop, Vue} from "vue-property-decorator"
import Portal from "@/managers/portal"
import {IStudentProfile} from "@chaosinsight/yogipeople-portalclient"
import Countries from "@/assets/data/countries"
import NotificationMessage from "@/components/NotificationMessage.vue"

@Component({components: { NotificationMessage } })
export default class AddCard extends Vue {
	@Prop()
	private profile!: IStudentProfile

	private Countries = Countries
	private errMessage: string | null = null

	private address1: string | null = null
	private address2: string | null = null
	private city: string | null = null
	private zip: string | null = null
	private country: string = ""

	private stripe = (window as any).Stripe(process.env.VUE_APP_STRIPE_SECRET_KEY)
	private elements = this.stripe.elements()
	private card: any | null = null

	private savingCard: boolean | null = null
	private disableAdd: boolean | null = null
	private secret: string = ""
	private cardOptions = {
		style: {
			base: {
				"color": "black",
				"fontSize": "16px",
				"fontWeight": "300",
				"::placeholder": {
					color: "#8a8a8f",
				},
			},
			invalid: {
				color: "#dbdbde",
			}
		},
		hidePostalCode: true
	}
	private created(): void {
		this.getPaymentIntent()
		}
	private get isFormValid(): boolean {
		return !!this.address1 && !!this.city && !!this.zip && !!this.country
	}

	private mounted(): void {
		this.card = this.elements.create("card", this.cardOptions)
		this.card.mount(this.$refs.card as HTMLElement)
	}

	/**
	 * Create new payment method
	 */
	private getPaymentIntent(): void {
		Portal.client.call.payments.paymentMethodsPost()
		.response
		.then(res => {
			this.secret = res.Secret
		})
		.catch(reason => {})
	}
	private handleCardSetup(): void {
		this.stripe.handleCardSetup(this.secret, this.card, {
			payment_method_data: {
			billing_details: this.billingDetails()
			}
			}).then((result: any) => {
				if (result.error)
				this.errMessage = result.error.message
				else {
					this.savingCard = true
					setTimeout(() => this.$emit("getCards"), 2200)
					}
				this.disableAdd = null
				})
	}


	// create billing details for user
	private billingDetails(): object {
		return {
			address: {
				city: this.city,
				country: this.country,
				line1: this.address1,
				line2: this.address2,
				postal_code: this.zip
			}
		}
	}
}
