






























import {Component, Prop, Vue, Watch} from "vue-property-decorator"
import Portal from "@/managers/portal"
import {ITrainingSession, TrainingSessionState, TrainingSessionType} from "@chaosinsight/yogipeople-portalclient"
import {ITrainingSessionHistory} from "@chaosinsight/yogipeople-portalclient/dist/extensions/trainingSession"
import MessageBar from "@/components/MessageBar.vue"

@Component({ components: { MessageBar }} )
export default class MySessions extends Vue {
	@Prop()
	private reloadHistory!: boolean

	private sessionsHistory: ITrainingSessionHistory | null = null
	private sessionsRange: ITrainingSessionHistory | null = null

	private totalPages: number = 0
	private currPage: number = 1
	private perPage: number = 5

	private sessionState: object = TrainingSessionState

	private get isStudent(): boolean | null {
		return Portal.isStudent
	}

	@Watch("reloadHistory")
	private reload(): void {
		this.getSessionsHistory()
		this.$emit("historyReloaded")
	}

	private mounted(): void {
		this.getSessionsHistory()
	}

	/**
	 * Get sessions history
	 */

	private getSessionsHistory(): void {
		this.isStudent
			? this.getForStudent()
			: this.getForTrainer()
	}

	/**
	 * Sessions history for Student/Trainer
	 */

	private getForStudent(): void {
		Portal.client.call.trainingSession.getForStudent()
			.response
			.then(res => {
				this.sessionsHistory = (res as any).reverse()
				this.totalPages = Math.ceil((this.sessionsHistory as any).length / this.perPage)
				this.historyPortion(this.currPage)
			})
	}

	private getForTrainer(): void {
		Portal.client.call.trainingSession.getForTrainer()
			.response
			.then(res => {
				this.sessionsHistory = (res as any).reverse()
				this.totalPages = Math.ceil((this.sessionsHistory as any).length / this.perPage)
				this.historyPortion(this.currPage)
			})
	}

	/**
	 * Get portion of Sessions History
	 */

	private historyPortion(nextPage: number): void {
		nextPage = nextPage > this.totalPages
			? this.totalPages
			: nextPage < 1 ? 1 : nextPage
		const from: number = (nextPage - 1) * this.perPage
		const to: number = from + this.perPage
		this.sessionsRange = (this.sessionsHistory as any).slice(from, to)
		this.currPage = nextPage
	}

	/**
	 * Format date
	 */

	private formatDate(date: string): string {
		const d = new Date(date)
		return `${d.toLocaleDateString()}, ${this.amPm(d.getHours(), d.getMinutes()).toUpperCase()}`
	}

	/**
	 * Format training type and duration
	 */

	private formatTypeDuration(typeId: number, duration: number): string {
		const t = TrainingSessionType[typeId]
		const d = duration === 30 ? 25 : 55
		return `${this.capitalize(t)}, ${d}min`
	}

	private capitalize(str: string): string {
		return str.charAt(0).toUpperCase() + str.slice(1)
	}

	/**
	 * Convert hour in am/pm format
	 */

	private amPm(hour: number, min: number): string {
		const m = (min && min < 10) ? this.leadZero(min) : min
		if (hour > 12)
			return `${hour % 12}${m ? ":" + m : ""}pm`
		else if (hour < 12)
			return `${hour}${m ? ":" + m : ""}am`
		return `12${m ? ":" + m : ""}pm`
	}

	/**
	 * Lead zero
	 */

	private leadZero = (x: number): string => x < 10 ? x < 0 ? `00` :	`0${x}` : `${x}`

	/**
	 * Update rating for completed session
	 */

	private updateRating(session: ITrainingSession): void {
		if (session.State === 3 && this.isStudent)
			this.$emit("updateRating", session)
	}

	/**
	 * Allow click for student and if session is completed
	 */

	private isCompletedAndStudent(state: number): boolean | null {
		return state === 3 && this.isStudent
	}
}
