





























import {Component, Prop, Vue} from "vue-property-decorator"
import {IStudentProfile, ITrainerProfile, ITrainingSession} from "@chaosinsight/yogipeople-portalclient"
import UserAccount from "@/components/UserAccount.vue"
import UserPayment from "@/components/UserPayment.vue"
import UserSessions from "@/components/UserSessions.vue"
import UserHelp from "@/components/UserHelp.vue"
import UserLegal from "@/components/UserLegal.vue"
import UserProfile from "@/components/UserProfile.vue"
import CardPlans from "@/components/CardPlans.vue"

@Component({ components: { UserAccount, UserHelp, UserLegal, UserPayment, UserSessions, UserProfile, CardPlans } })
export default class MenuItems extends Vue {
	@Prop()
	public profile!: IStudentProfile | ITrainerProfile

	@Prop()
	private activeItem!: string

	@Prop()
	private reloadHistory!: boolean

	/**
	 * Listen "updateRating" from UsersSessions and emit to StudentLanding
	 */

	private updateRating(session: ITrainingSession): void {
		this.$emit("updateRating", session)
	}

	/**
	 * Listen "historyReloaded" from UsersSessions and emit to StudentLanding
	 */

	private historyReloaded(): void {
		this.$emit("historyReloaded")
	}
}
